*{box-sizing:border-box;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;-o-box-sizing:border-box;-ms-box-sizing:border-box;}
body{font-family: 'Poppins', sans-serif; font-style:normal; font-weight:normal; font-size:18px; line-height:32px; color:#666666; margin:0;overflow-x:hidden;}
p,h1,h2,h3,h4,h5,h6,ul,li,a{margin:0;padding:0;line-height:1}
a{text-decoration:none;outline:none;color:#f16a54;}
a:hover{color:#333;text-decoration:none}
img{vertical-align:top;max-width:100%;height:auto;border:none}
input,select,textarea{font-family: 'Poppins', sans-serif;font-size:13px;color:#000}
iframe{max-width:100%;border:none;width:100%;height:100%;}
textarea{resize:none;min-height:82px}
input:focus,textarea:focus,select:focus,button:focus{outline:none}
textarea,input[type="text"],input[type="password"],input[type="datetime"],input[type="datetime-local"],input[type="date"],input[type="month"],input[type="time"],input[type="week"],input[type="number"],input[type="email"],input[type="url"],input[type="search"],input[type="tel"],input[type="color"]{border:1px solid #dddddd;background:#fff;padding:0 13px;box-shadow:none;height:49px;max-width:100%;border-radius:0px;width:100%}
input[type="search"]{-webkit-appearance:none}
textarea{line-height:normal;padding:9px;width:100%}
textarea:focus,input[type="text"]:focus,input[type="password"]:focus,input[type="datetime"]:focus,input[type="datetime-local"]:focus,input[type="date"]:focus,input[type="month"]:focus,input[type="time"]:focus,input[type="week"]:focus,input[type="number"]:focus,input[type="email"]:focus,input[type="url"]:focus,input[type="search"]:focus,input[type="tel"]:focus,input[type="color"]:focus{border:2px â€‹solid #f16a54 !important;}
select{border:1px solid #dddddd;background:#fff;max-width:100%;border-radius:3px;padding:15px 13px}
input[type="image"],input[type="submit"],input[type="button"],button{border:medium none;border-radius:0px;color:#fff;cursor:pointer;display:inline-block;font-size:14px;padding:0px 23px;text-transform:uppercase;height:40px;transition:all 0.3s ease-in-out 0s;font-weight:bold ;appearance:normal;-webkit-appearance:none;max-width:100%; background: #F16A54; border-radius: 5px;}
input[type="image"]:hover,input[type="submit"]:hover,input[type="button"]:hover,button:hover{background-color:#555}
input[type="image"]:active,input[type="submit"]:active,input[type="button"]:active,button:active{box-shadow:inset 0 0 12px rgba(0,0,0,0.2)}
.wrapper{margin:0 auto;padding:0 50px;max-width: 1540px /*1440px*/;}

